import React from "react";
import Image from "../Image";
import "../style.css";


const IMAGES= [
  "TakoLuka.jpg",
  "TakoLuka1.jpg",
  "TakoLuka2.jpg",
  "TakoLuka3.jpg",
  "TakoLuka4.jpg",
  "TakoLuka5.jpg",
  "TakoLuka6.jpg",
  "TakoLuka7.jpg",
  "TakoLuka8.jpg"
];

  
  function shuffle(IMAGES) {
    for (let i = IMAGES.length - 1; i > 0; i--) {
      let j = Math.floor(Math.random() * (i + 1)); 
      [IMAGES[i], IMAGES[j]] = [IMAGES[j], IMAGES[i]]; 
    }
  }



class GameCard extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      score: 9,
      images: false
    }
    this.updateState = this.updateState.bind(this);
  };



  updateState() {
    this.setState({ score: (this.state.score - 1) });
    if(this.state.score == 1){
      this.handleWinGame();
      this.setState({score: 9});
     }
    this.setState({images: true});
    console.log(this.state.images)
    
    shuffle(IMAGES);

    //if the image is already clicked, the game is lost, the score is reset to 9 and the images are set back to false,
    //however this function I made makes it so that when one image is clicked, ALL images are set to true
  
    // if (this.state.images != false) {
    //   this.handleLoseGame();
    //   this.setState({score: 9});
    //   this.setState({images: false})
    // }

};  

handleWinGame(){
   
      alert("Congratulations! You win! Play again?");
    
}

// handleLoseGame(){
//   alert("Oh no! You clicked on the same image twice! Better luck next time!")
// }
    


  render() {
    return (
      
      <div>
          <p className="header">Clicky <div className="score"> {this.state.score}</div></p>
          
        <div className="grid-container">
            
         
              {IMAGES.map(image => (
                
                  <div onClick={this.updateState}>
                   
                    <Image  source={image} key={image}> {this.state.images} </Image> 
                  </div>
                
              ))}

          </div>

         
                


        
         
      </div>



    );
  };
};



export default GameCard;